/* Panel Layouts */
.layoutTicket {
  position: relative;
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-areas: "content"
  "inputs";
  grid-template-columns: 100%;
  grid-template-rows: auto 160px;
}

.layoutList {
  position: relative;
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-areas: "content"
  "inputs";
  grid-template-columns: 100%;
  grid-template-rows: auto 100px;
}

.layoutFull {
  position: relative;
  display: block;
  width: 100%;
  height: auto;
}

  /* Panels/Components */

.layContent {
  position: relative;
  grid-area: content;
  overflow-y: scroll;
}
.layInputs {
  position: relative;     
  grid-area: inputs;
  padding: 2px;
}